body{background: #E0E7E9;}
ul{padding: 0;}li{list-style: none;}
/* main ****************************/
.App{
  margin: 0 auto;
  max-width: 768px;
  padding: 1rem;
  background: white;
  border-radius: 10px;
}
/************** public**************/
.mt{
  font-size: 24px;
  padding: 1rem;
  color: #354649;
  font-weight: bold;
  box-sizing: border-box;
}
.hr{
  border-bottom: 1px solid #354649;
}

/* Todolist ********/

.lt{
  display: block;
  font-size: 20px;
  font-weight: bold;
  color: #354649;
  padding: 1rem;
}
.X{
  margin-left: 1rem;
  cursor: pointer;
  transition: all 1s;
}
.checkbox{
  display: inline-block;
  margin: 0 1rem 0 0;
  width: 1rem;
  height: 1rem;
  border: 1px solid #354649;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
/*인풋체크박스 기본상태*/
input[type="checkbox"]{
  margin: 0 1rem 0 0;
  appearance: none;
  width: 1rem;
  height: 1rem;
  border: 1px solid #354649;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

/* 인풋체크박스 클릭상태 */
input[type="checkbox"]:checked{
  background-color:crimson;
}
